/**
 * Site header
 */
#logo {
  margin-top: 8px;
  float: left;
}

.site-header {
  border-bottom: 1px solid $grey-color-light;
  min-height: 56px;

  // Positioning context for the mobile navigation icon
  position: relative;
}

.site-title {
  font-size: 24px;
  font-weight: 300;
  line-height: 56px;
  letter-spacing: -1px;
  margin-bottom: 0;
  float: left;
}

.site-nav {
  float: right;
  line-height: 56px;
  font-size: 20px;
  letter-spacing: -1px;

  .page-link {
    color: $grey-color;
    font-weight: 300;
    line-height: $base-line-height;

    // Gaps between nav items, but not on the last one
    &:not(:last-child) {
      margin-right: 20px;
    }
  }

  @include media-query-min($on-screen) {
    position: absolute;
    top: 5px;
    right: $spacing-unit / 2;
    background-color: $background-color-white;
    text-align: right;

    .page-link {
      display: block;
      padding: 5px 10px;

      &:not(:last-child) {
        margin-right: 0;
      }
      margin-left: 20px;
    }
  }
}

/**
 * Homepage
 */

.outer {
  display: flex;
  overflow: hidden;
  height: 100vh;
}
.outer .navigation {
  overflow: hidden;
}
.navigation {
  @extend .page-content;
  max-width: 300px;
  min-width: 130px;
  flex: 0 1 auto;
  //   border: 1px solid black;
  //   margin: 40px 0 auto auto;
  //   height: 600px;
  position: relative;
  .nav-wrapper {
    @extend .wrapper;
    .tip-enum {
      display: block;
      list-style: none;
      width: 80px;
      right: 17px;
      top: 29%;
    }
   
  }
}
.homepage-img {
  width: 130px;
  height: 80px;
  img{
    width: 90px;
  }
}

.homepage-img img {
  background-color: rgba(0, 0, 0, 0);
}

/**
 * Page content
 */
#article {
  @include media-query-min($on-screen) {
    padding-bottom: 4em;
  }
}
.wrapper {
  margin-bottom: 98px;
  position: relative;
 
  .hitokoto {
    position: absolute;
    top: 48px;
    right: 0;
    padding-left: 0.6rem;
    padding-right: 0.4rem;
    font-style: italic;
    font-size: 12px;
    background-color: #eee;
    border-radius: 3px;
  }

}

.page-content {
  flex: 1 1 auto;
  overflow: scroll;
  height: 100vh;
  padding-top: $spacing-unit;
  padding-bottom: 0 !important;

  @include media-query-max($on-screen) {
    background-color: $background-color-white;
    padding-bottom: 3em;
  }
  @include media-query-min($on-screen) {
    .wrapper {
      box-shadow: 0px 10px 20px 0px rgba(236, 236, 236, 0.86);
      article {
        width: 700px;
        margin: 0 auto;
        padding: 60px 0;
      }
      
    }
  }
}

.user-info{
  text-align: center;
  .project{
    padding-top: 16px;
    font-size: 12px;
    a{
      padding: 1px; 
    }
    a:hover{
      border-bottom: 2px solid #00ae50;
      background-color: rgba($color: #00ae50, $alpha: 0.1)

    }
  }
}

.page-heading {
  font-size: 20px;
}

.home {
  margin-top: -1.5em;
  h2 {
    color: lighten($text-color, 10%);
  }
}

.post-list {
  list-style-position: inside;
  margin-left: 2em;
  color: $text-color;
  a {
    color: lighten($text-color, 10%);
    &:hover {
      border-bottom: solid 1px $grey-color;
    }
  }
  @include media-query-max($on-screen) {
    padding-left: 0;
  }
}

.post-meta {
  font-size: $meta-font-size;
  color: $grey-color;
  margin-top: -20px;
  a:not(:first-child) {
    margin-left: 10px;
  }
}

.post-link {
  font-size: 16px;
}
/* Excerpt */
.excerpt-source{
  text-align: right;
  padding-right: 2rem;
  font-size: 14px;
  font-style: italic;
  margin-bottom: 25px;
}

/**
 * Posts
 */
.post-header {
  margin-bottom: $spacing-unit;
}
.post a:hover {
  border-bottom: solid 1px $grey-color;
}

div.highlight {
  background-color: $background-color-gray;
  @include media-query-max($on-screen) {
    width: 100vw;
    position: relative;
    left: 50%;
    right: 50%;
    margin-left: -50vw;
    margin-right: -50vw;
  }
  @include media-query-min($on-screen) {
    margin: 0 -50px;
    > pre {
      padding: 20px 50px;
    }
  }
}

summary + .highlight {
  margin-top: $spacing-unit / 1.5;
}

/* 响应式 */
@media screen and (max-width: 800px) {
    .navigation{
        display: none;
    }
    .page-content{
        padding-top: 0;
    }
}